.principeContainer {
  margin: 0 100px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.titleBanner {
  display: flex;
  position: relative;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  background: linear-gradient(to bottom, #fe5555, #c64747);
  color: white;
  border-radius: 5px;
}

.titleBanner .fa-angle-down {
  position: absolute;
  top: 20px;
  right: 10px;
  transition: all 1s;
}
.titleBanner .fa-angle-down.reverse {
  transform: rotate(180deg);
}
.titleCollapse {
  margin-left: 20px;
}

.collapseDescription {
  background-color: rgb(247, 247, 247);
  margin-top: 0;
  padding: 10px;
}
