.mainContainer {
  display: flex;
  margin: 0 50px 0 50px;
  flex-direction: column;
  gap: 30px;
}
.mainContainer figure {
  margin: 0;
  position: relative;
}

.imgHdp {
  width: 100%;
  border-radius: 40px;
  box-shadow: rgba(0, 0, 0, 0.25) 0 4px 4px 0px;
}

.mainContainer figure figcaption {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "Montserrat";
  color: white;
  font-weight: 700;
  font-size: 48px;
  white-space: nowrap;
}

.annoncesContainer {
  max-width: 100%;
  background-color: rgb(240, 240, 240);
  border-radius: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  column-gap: 20px;
  padding: 30px;
}

.annoncesContainer a {
  text-decoration: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.cardAnnonce {
  max-width: 100%;
  background: linear-gradient(to bottom, #fe5555, #c64747);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 10px;
}
.cardAnnonce img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 20px;
}

.cardAnnonce h4 {
  color: white;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
