.picture.active {
  display: block;
  transform: all 1s;
}
.picture {
  display: none;
}
.galleryImage {
  width: 100%;
  height: 415px;
  object-fit: cover;
}

.Gallery {
  position: relative;
}

.Gallery .arrow {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
  color: azure;
  top: 40%;
}

.Gallery .fa-solid {
  font-size: 90px;
}
