* {
  font-family: "Montserrat";
}
.annoncePageMC {
  margin: 50px;
}
.profilInfoWithStars {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

.titleAnnonce h1 {
  color: rgba(255, 96, 96, 1);
  font-size: 36px;
  margin: 0.5em 0 0 0;
  font-weight: 500;
}

.titleAnnonce h3 {
  margin: 0;
  margin-bottom: 0.5em;
}

.annonceProfil {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  gap: 30px;
  flex-wrap: wrap;
  align-items: center;
  align-content: flex-start;
}

.profilPicture {
  width: 64px;
  height: 64px;
  border-radius: 50px;
}

.nameProfil {
  color: rgba(255, 96, 96, 1);
  font-size: 18px;
}
.profilWithRate {
  display: flex;
  flex-direction: column;
  gap: 45px;
}
.rating {
  display: flex;
  justify-content: flex-end;
}
.allTags {
  display: flex;
  gap: 20px;
}
.annoncePageTag {
  background-color: rgba(255, 96, 96, 1);
  font-weight: 700;
  padding: 5px 20px;
  border-radius: 20px;
  color: white;
  font-size: 14px;
}

.collapseContainer {
  display: flex;
  flex-direction: row;
  gap: 30px;
}

.collapseContainer .collapseBar {
  width: 100%;
}
