header {
  margin: 50px 50px 0 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.NavHeader ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 20px;
}

.NavHeader a {
  color: black;
  text-decoration: none;
}

.active {
  text-decoration: underline;
}
.NavHeader a:hover {
  text-decoration: underline;
}

footer {
  background-color: black;
  width: 100%;
  height: 209px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
footer p {
  color: white;
  margin-top: 30px;
  font-size: 24px;
}
